.tooltip-base {
  position: relative;
  display: inline-block;
}
.tooltip-base:hover .tooltiptexto {
  visibility: visible;
  opacity: 1;
}
.tooltip-base .tooltiptexto {
  all: unset;
  visibility: hidden;
  font-size: 1.2rem;
  text-transform: uppercase;
  width: auto;
  background-color: #0063c1;
  color: #fff;
  text-align: center;
  font-weight: bold;
  border-radius: 6px;
  padding: 9px;
  position: absolute;
  z-index: 1;
  top: -200%;
  /* Posiciona el tooltip bajo del elemento */
  left: 50%;
  transform: translateX(-50%);
  opacity: 0;
  transition: opacity 0.3s;
}
.tooltip-base .tooltiptexto::after {
  content: " ";
  position: absolute;
  top: 100%;
  /* At the bottom of the tooltip */
  left: 50%;
  margin-left: -5px;
  border-width: 5px;
  border-style: solid;
  border-color: #0063c1 transparent transparent transparent;
}

